@import url(https://use.fontawesome.com/releases/v5.15.4/css/all.css);
@import url(https://fonts.googleapis.com/css2?family=Oswald&display=swap);
* {
	font-family: 'Oswald', Arial, Helvetica, sans-serif;
  margin: 0;
  padding: 0;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

:root {
  --main-color: #ffcc33;
  --main-var-color: #e2b01b;
  --secondary-color: #373542;
  --light-color: #999;
  --dark-color: #000;
  --random-color: #aeaeae;
  --neutral-color: #f7f7f7;
  --text-color: #3E4548;
  --danger-color: #C85C5C;
  --safe-color: #17c560;
  --border-color: #e7e3e3;
}
.auth {
	display: flex;
	flex-direction: column;
	align-items: center;
}

.auth-heading {
	text-align: center;
	text-transform: uppercase;
	font-size: 24px;
	font-weight: 200;
	padding-top: 1%;
}

.auth-form {
	min-height: 40vh;
}

.form-group {
	margin: 2% 0;
	text-align: center;
}

.btn-submit {
	color: var(--main-color);
	border: none;
	padding: 15px 20px;
	border-radius: 15px;
	margin: 10px;
	cursor: pointer;
	font-weight: bold;
	letter-spacing: 2px;
	text-transform: uppercase;
	background-color: var(--text-color);
	border: 2px solid var(--main-color);
	align-items: flex-end;
}

.btn-submit:hover {
	color: var(--text-color);
	background-color: white;
}

.switch-form span {
	color: var(--main-color);
	background-color: var(--light-color);
	border-radius: 10px;
	padding: 0 10px;
}

.switch-form a {
	text-decoration: none;
	cursor: pointer;
}

.error {
	color: var(--danger-color);
	font-size: 14px;
	text-align: center;
	text-transform: uppercase;
	font-weight: bold;
}

.form {
	display: flex;
	justify-content: center;
}

.auth-input {
	font-size: 26px;
	padding-left: 2%;
	color: var(--random-color);
}

.notification-world-stadia {
	color: var(--main-color);
	text-align: center;
	padding-bottom: 1%;
}


.notification-world-stadia-message {
    color: white;
    text-align: center;
}
.header-section {
	background-color: var(--secondary-color);
	background-image: url(/static/media/header.d3515965.png);
}

.header-row {
	display: flex;
	justify-content: center;
}

.header-row,
.header-row li {
	list-style: none;
	margin: 10px;
	font-size: 20px;
	font-weight: bold;
	text-transform: uppercase;
	transition: transform .25s cubic-bezier(.7, .98, .86, .98), box-shadow .25s cubic-bezier(.7, .98, .86, .98);
	cursor: pointer;
}

.header-row li a {
	text-decoration: none;
	color: white;
	display: block;
	margin: 0 10px;
	cursor: pointer;
}

.header-row li a:hover {
	transform: scale(1.1);
	color: var(--main-color);
}

.header-row .active {
	border-bottom: 2px solid var(--main-color);
}

.notification-world-stadia {
	color: var(--main-color);
	text-align: center;
	padding-bottom: 1%;
}

