}

.btn-like:hover,
.btn-comment:hover {
	color: var(--text-color);
	background-color: white;
}

.buttons {
	display: flex;
	justify-content: space-between;
}

.btn-edit {
	color: var(--main-color);
	background-color: var(--text-color);
	border: 2px solid var(--main-color);
}

.btn-delete {
	color: var(--main-color);
