table {
	width: 100%;
	text-align: left;
	border-collapse: collapse;
	font-size: 20px;
	margin-top: 5%;
	margin-bottom: 8%;
}

table tbody tr {}


table tbody tr th,
table tbody tr td {
	border: 1px solid var(--border-color);
	padding: 5% 2%;
	font-weight: normal;
}

table tbody tr th {
	width: 35%;
	text-transform: uppercase;
}

table tbody tr td {
	width: 65%;
	background-color: var(--neutral-color);
	text-transform: capitalize;
}

.info-image-container {
	width: 25%;
}

.info-text-container {
	width: 70%;
	text-align: left;
}

.info-text-container h2 {
	text-transform: uppercase;
}

.stadium-comments {
	padding: 2% 5%;
	width: 80%;
}

.stadium-comments-all-heading h2 {
	text-transform: uppercase;
}

.stadium-comments-all .comment-card {
	display: flex;
	border: 1px solid var(--border-color);
}

.stadium-comments-all .image-container {
	width: 35%;
}

.stadium-comments-all .image-container img {
	height: 100%;
	width: 150px;
	padding: 5% 0;
	object-fit: contain;
}

.stadium-comments-all .comment-container {
	width: 60%;
	padding: 5% 0;
	text-align: left;
}

.stadium-comments-all .comment-container .comment-date {
	background-color: var(--secondary-color);
	color: var(--main-color);
	padding: 0 2%;
	font-size: 14px;
}

.stadium-comments-all .comment-container .comment-user h3 a {
	color: var(--secondary-color);
	text-transform: uppercase;
	text-decoration: none;
}

.stadium-comments-all .comment-container .comment-user h3 a:hover {
	color: var(--main-color);
}

.stadium-comments .form-input,
.stadium-comments .form-textarea {
	width: 80%;
	resize: none;
}

.stadium-comments .form-input {
	background-color: var(--border-color);
	text-transform: uppercase;
	letter-spacing: 2px;
}

.stadium-comments .form-textarea,
.stadium-comments .form-input {
	font-size: 26px;
	padding-left: 2%;
	color: var(--random-color);
}


