}

.far.fa-envelope,
.fab.fa-linkedin {
	margin-right: 2%;
}

.footer-outro {
	text-align: center;
	background-color: var(--secondary-color);
	color: var(--main-color);
}

.footer-about .footer-heading {
	color: var(--main-color);
}

@media screen and (max-width: 700px) {
	.footer-wrapper {
		display: none;
	}

	.footer-wrapper,
	.footer-outro {
		font-size: 14px;
	}
}
.stadium {
	width: 100%;
	height: 350px;
	object-fit: cover;
	transform: scale(1);
	/* -webkit-filter: grayscale(100%);
	filter: grayscale(100%); */
	transition: .5s ease-in-out;
}

.stadium:hover {
	transform: scale(1.05);
	filter: grayscale(0);
	border: 1px solid white;
	z-index: 1;
}

.stadium-image {
	width: 100%;
	height: 350px;
	object-fit: cover;
}

.stadium-name {
	color: var(--main-color);
	text-align: center;
	font-weight: bold;
	padding: 2% 0;
	background-color: var(--secondary-color);
	display: none;
	text-transform: uppercase;
}

.stadium:hover>.stadium-name {
	display: block;
	transition: .5s ease-in-out;
}

@media screen and (max-width: 1100px) {
	.stadium {
		height: auto;
	}
}

.container {
	display: flex;
	flex-direction: column;
	margin-bottom: 5%;
}

.form {
	display: flex;
	justify-content: center;
}

.form-item {
	padding: 20px;
}

.search-input {
	width: 100%;
	font-size: 26px;
	padding-left: 2%;
	color: var(--random-color);
}

.form-input {
	width: 100%;
	font-size: 26px;
	padding-left: 2%;
	color: var(--random-color);
}

.filter-list {
	display: flex;
	flex-direction: row;
	justify-content: center;
}

.filter-list,
.filter-list li {
	list-style: none;
	margin: 10px;
	font-size: 14px;
	text-transform: uppercase;
	transition: transform .25s cubic-bezier(.7, .98, .86, .98), box-shadow .25s cubic-bezier(.7, .98, .86, .98);
	cursor: pointer;
}

.filter-list li a {
	text-decoration: none;
	color: var(--secondary-color);
	display: block;
	padding: 20px 20px;
	margin: 0 10px;
	cursor: pointer;
	border: 1px solid var(--random-color);
}

.filter-list li a:hover {
	background-color: var(--main-color);
	border: 1px solid var(--main-color);
	transition: transform .25s cubic-bezier(.7, .98, .86, .98), box-shadow .25s cubic-bezier(.7, .98, .86, .98);
}

.active {
	background-color: var(--main-color);
	border: 1px solid var(--main-color);
}

.list-items-section {}


.list-items-results {
	text-align: center;
	text-transform: uppercase;
	font-size: 24px;
	font-weight: 200;
}

.list-items {
	display: flex;
	flex-wrap: wrap;
	grid-gap: 1px;
	gap: 1px;
	width: 100%;
	padding: 1% 5%;
	justify-content: center;
}

.list-items article {
	width: calc((100% - 24px) / 5);
}

@media screen and (max-width: 1100px) {

	.filter-list,
	.filter-list li {
		margin: 1%;
		width: auto
	}

	.filter-list li a {
		font-size: 12px;
		padding: 15px;
	}

	.list-items article {
		width: calc((100% - 20px) / 4);
	}

}

@media screen and (max-width: 800px) {

	.filter-list,
	.filter-list li {
		margin: 1%;
	}

	.filter-list {
		flex-wrap: wrap;
	}

	.filter-list li a {
		font-size: 10px;
		padding: 7px;
	}

	.list-items article {
		width: calc((100% - 17px) / 3);
	}
}

@media screen and (max-width: 600px) {


	.filter-list,
	.filter-list li {
		margin: 0.5%;
	}

	.filter-list li a {
		font-size: 8px;
		padding: 10px;
	}

	.filter-list {
		flex-wrap: wrap;
	}

	.search-input {
		font-size: 14px;
	}

	.list-items article {
		width: calc((100% - 12px) / 2);
	}
}
